import React, { Component } from 'react';
// import './App.css';
import './NewApp.css';
import 'antd/dist/antd.css';
import './animations.css';

import * as firebase from 'firebase';
import config from './firebase';

//ANT design
import { Row, Col, Layout, Icon } from 'antd';

import Home from './Components/Home';
import Loading from './Components/Loading';
import EnCourse from './Components/EnCourse';
import Menu from './Components/Menu';
import ProfilUser from './Components/ProfilUser';
import HeaderMenu from './Components/HeaderMenu';

import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

const {
  Header, Footer, Content,
} = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    firebase.initializeApp(config);
        this.state = {
            listeDeBase: [],
            data: [],
            titreListe: [],
            currentTitre: '',
            choixComponent: 'menu',
            currentCategory: '' || 'Ma super catégorie',
            numChildCategory: 0,
            isSignedIn: false,
            clicked: false, 
            loading: true,
            goHome: false,
            isCategory: true,
            clickedText : false,
            indexItem: null,
            isToggleSwitchType: false,
            help: true,
            inverseProduit: true,
            userId: '' || 'Mikael',
            largeur: 0,
            hauteur: 0,
            lastMinuteItem: false //verifie si un item a ete ajoute a la derniere minute
        };
      this.delete = this.delete.bind(this);
      this.deleteCategory = this.deleteCategory.bind(this);
      this.createNewItem = this.createNewItem.bind(this);
      this.addNewListTitre = this.addNewListTitre.bind(this);
      this.deconnexion = this.deconnexion.bind(this);
}

uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'popup',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,

  ],
  callbacks: {
    // Avoid redirects after sign-in.
    signInSuccessWithAuthResult: () => false
  }
};

componentDidMount() {
  //détection du changement de taille d'écran
  this.handleResize();
  window.addEventListener('resize', this.handleResize);

  this.unregisterAuthObserver = firebase.auth().onAuthStateChanged(
      (user) => {
        if(user) {
          this.setState({isSignedIn: !!user, userId: user.displayName})
        } else {
          return null
        }
      }
  );
  // console.log(this.unregisterAuthObserver());
  
}

componentWillUnmount() {
  window.removeEventListener('resize', this.handleResize); //on enlève l'évènement qui regarde la taille de la fenêtre
  this.unregisterAuthObserver();
}

  animFormButton = (button) => {
    button.classList.add('formButton-active');
    setTimeout(() => {
        button.classList.remove('formButton-active');
    }, 500);
  }

  animFormElt = (elt) => {
    elt.classList.add('message-enter');
  }

  getDataBase = () => {
    setTimeout(() => {
    const user = this.state.userId;
    const ref = firebase.database().ref(`${user}`);
        ref.on('value', snapshot => {
          const listItem = [];
          if(snapshot.val() !== null) {
            firebase.database().ref(`${user}/user/`).on('value', snapshot => {
              this.setState({ 
                help: snapshot.val().help,
                inverseProduit: snapshot.val().inverseProduit
               });
            });
            Object.keys(snapshot.val()).map(item =>
              listItem.push(item)
            );
            this.setState({
              listeDeBase: listItem,
              data: snapshot.val(),
              loading: false
              });
            } else {
              firebase.database().ref(`${user}`).set({
                user: {
                  userName: user,
                  help: true
                }
              });
              this.setState({
                loading: false
            });
          }
        });
      }, 2000);
  }

  //détecter la taille de la fenêtre
  handleResize = () => this.setState({
    hauteur: window.innerHeight,
    largeur: window.innerWidth
  });

  // on ou off de l'aide
  toggleSwitchHelp = (checked) => {
    const user = this.state.userId;
    const help = this.state.help;
    this.setState({ help : checked === true ? false : true });
    let updates = {};
    updates[`${user}/user/help`] = !help;
    return firebase.database().ref().update(updates);
  }

  // on ou off pour inverser l'ordre des produits
  toggleSwitchInverseProduit = (checked) => {
    const user = this.state.userId;
    const inverseProduit = this.state.inverseProduit;
    this.setState({ inverseProduit : checked === true ? true : false });
    let updates = {};
    updates[`${user}/user/inverseProduit`] = !inverseProduit;
    return firebase.database().ref().update(updates);
  }

  // Regarde si un produit est checked en mode collapse
  isChecked = (item, id) => {
    const user = this.state.userId;
    const collapse = document.querySelector(`#elt-${item.key}`);
    // console.log(collapse);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    // console.log(currentCategory);
    const currentTitre = this.state.currentTitre;
    const newData = this.state.data;
    Object.keys(newData).map(elt => Object.keys(newData[elt]).map(product => newData[elt][product].key === item.key ? {...product, ...{checked : true} } : product));
    item.checked = !item.checked;

    this.setState({ data: newData });
    // A ESSAYER POUR METTRE A JOUR LE STATE
    // this.setState(state => {
    //   const data = state.data.map((item, j) => {
    //     if (j === id) {
    //       return item.checked = !item.checked;
    //     } else {
    //       return item;
    //     }
    //   });

    //   return {
    //     data,
    //   };
    // });

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${user}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
}
  // Delete un produit
  delete(item, id){
    const user = this.state.userId;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    const contentElt = collapse.parentElement;
    const currentTitre = this.state.currentTitre;
    
    this.setState({ 
      indexItem: `elt-${item.item}`,
      numChildCategory: contentElt.childElementCount
  });
  //A ESSAYER POUR METTRE A JOUR LE STATE
  // this.setState(state => {
  //   const data = Object.keys(state.data).filter(item => item.id !== id);

  //   return {
  //     data,
  //   };
  // });

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });
    if(contentElt.childElementCount === 1) {
      firebase.database().ref(`${user}/${currentTitre}/category/${index}`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
        const indexCat = snapshot.key;
          setTimeout(() => {
            firebase.database().ref(`${user}/${currentTitre}/category/${indexCat}`).remove();
          }, 400);
      });
      setTimeout(() => {
        this.setState({ indexItem: null});
      }, 410);
    } else {
        firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
          snapshot.forEach(function(childSnapshot){
            setTimeout(() => {
              const indexItem = childSnapshot.key;
              firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems/${indexItem}`).remove();
            }, 400);
          });
        });
        setTimeout(() => {
          this.setState({ indexItem: null});
        }, 410);
    }
      
      
  }

  // delete une catégorie
  deleteCategory = (item, id) => {
    const user = this.state.userId;
    // console.log(item.titreCat);
    const collapse = document.getElementById(item.titreCat);
    // console.log(collapse);
    const currentTitre = this.state.currentTitre;

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === collapse.id ? childSnapshot.key : null;
      });
    });
    // console.log(index);

    firebase.database().ref(`${user}/${currentTitre}/category/${index}`).orderByChild('key').on('value', snapshot => {
        setTimeout(() => {
          firebase.database().ref(`${user}/${currentTitre}/category/${index}`).remove();
        }, 400);
    });
    setTimeout(() => {
      this.setState({ indexItem: null});
    }, 410);
  }

  deleteList = (item) => {
    const user = this.state.userId;
    this.setState({ indexItem: `elt-${item}`});

    setTimeout(() => {
      firebase.database().ref(`${user}/${item}`).remove();
    }, 400);

    setTimeout(() => {
      this.setState({ indexItem: null});
    }, 410);
    
  }

  //faire apparaitre/disparaitre la commentaire
  toggleComment = (item) => {
    const user = this.state.userId;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    const currentTitre = this.state.currentTitre;
    const newData = this.state.data;
    Object.keys(newData).map(elt => Object.keys(newData[elt]).map(product => newData[elt][product].key === item.key ? {...product, ...{show : true} } : product));
    item.show = !item.show;

    this.setState({data : newData});

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${user}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
  }

  // Ajouter un commentaire à un élément de la liste
  addComment = (event, item) => {
    event.preventDefault();
    const user = this.state.userId;
    const currentTitre = this.state.currentTitre;
    let commentName = event.target.value;
    item.comment = event.target.value;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;

    const newData = this.state.data;
    Object.keys(newData).map(elt => Object.keys(newData[elt]).map(product => newData[elt][product].key === item.key ? {...product, ...{comment : commentName} } : product));

    this.setState({data : newData});

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${user}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
  }

  // événement qui ferme le commentaire
  closeComment = (event, item) => {
    event.preventDefault();
    const user = this.state.userId;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    const currentTitre = this.state.currentTitre;
    const newData = this.state.data;
    Object.keys(newData).map(elt => Object.keys(newData[elt]).map(product => newData[elt][product].key === item.key ? {...product, ...{show : true} } : product));
    item.show = !item.show;

    this.setState({data : newData});

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${user}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
  }

  // Créer une catégorie dans la liste de course
  createNewCategory = (event) => {
    event.preventDefault();
    const user = this.state.userId;
    const currentTitre = this.state.currentTitre;
    const currentCategory = this.state.currentCategory;
    const formInput = document.querySelector('.form>input');
    const todoFormButton = document.querySelector('.form>button');
    let category = '';
    if(currentCategory === 'Ajout de dernière minute') {
      category = 'Ajout de dernière minute';
    } else {
      category = formInput.value;
    }
    
    if(formInput.value === '') {
      return null
    }
    //Arriver a insérer une catégorie sans exemple de produit!
    const data = {
      titreCat: category
    }

    this.setState(prevState => ({
      // listeDeBase: [...prevState.listeDeBase, category],
      currentCategory: category,
      isCategory: false
    }));
    
    firebase.database().ref(`${user}/${currentTitre}/category`).push({
      ...data
    });
    this.animFormButton(todoFormButton);
    formInput.value = '';

  }

  //prend la catégorie choisie au clic
  getCurrentCategory = titreCat => {
    const currentCategory = titreCat;
    this.setState({ currentCategory, isCategory: false });
  }

  // Bouton switch pour choisir entre produit et catégorie
  toggleSwitch = (checked) => {
    this.setState({ isCategory : checked === false ? false : true });
  }

  //bouton switch pour choisir entre collapse et liste
  toggleSwitchType = (checked) => {
    this.setState({ isToggleSwitchType : checked === false ? false : true });
}

  // Créer un élément de liste de course
  createNewItem = (event) => {
    event.preventDefault();
    const user = this.state.userId;
    const currentCategory = this.state.currentCategory;
    const currentTitre = this.state.currentTitre;
    const timestamp = Date.now();
    const todoFormInput = document.querySelector('.form>input');
    const todoFormButton = document.querySelector('#buttonSubmit');
    const newData = todoFormInput.value;
    const data = {
      item: newData,
      checked: false,
      key: timestamp,
      comment: '',
      show: false,
    };
    if(currentCategory === 'Ma super catégorie') {
      this.createNewCategory(event);
      return null;
    }

    if(newData !== '' && currentTitre !== '') {
      let index = '';
      firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
        snapshot.forEach(function(childSnapshot){
            return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
        });
      });

      firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).push({
        ...data
      });
    }
    
    this.animFormButton(todoFormButton);
    todoFormInput.value = '';
  }

  // Ajout d'un titre de liste de course
  addNewListTitre = event => {
    event.preventDefault();
    // const user = this.state.userId;
    const formInput = document.querySelector('#form-input');
    const todoFormButton = document.querySelector('#myButtonSubmit');
    const titre = formInput.value;
    if(formInput.value === '') {
      return null
    }
    this.setState(prevState => ({
      listeDeBase: [...prevState.listeDeBase, titre],
      currentTitre: titre,
      isCategory: true
    }));

    // firebase.database().ref(`${user}`).push({
    //   titre
    // });

    this.animFormButton(todoFormButton);
    formInput.value = '';
  }

  // Détecte quelle liste à été choisie
  choixListe = (e) => {
    const user = this.state.userId;
    const titreListe = e.target.id;
    const ref = firebase.database().ref(`${user}`);
    // console.log(titreListe);

    ref.on('value', snapshot => {
      this.setState({
        data: snapshot.val(),
        currentTitre: titreListe,
        clicked: true
      });
    });
  }

  // En Course --------> Check toute la catégorie
  checkCurrentCategory = titreCat => {
    console.log(titreCat);
  }

  // EN COURSE --------> Crée un item dans la catégorie derniere minute
  createNewItemLastMinute = event => {
    event.preventDefault();
    const user = this.state.userId;
    const currentCategory = this.state.currentCategory;
    const currentTitre = this.state.currentTitre;
    const lastMinuteItem = this.state.lastMinuteItem;
    const timestamp = Date.now();
    const todoFormInput = document.querySelector('.form>input');
    const todoFormButton = document.querySelector('#buttonSubmit');
    const newData = todoFormInput.value;
    const data = {
      item: newData,
      checked: false,
      key: timestamp,
      comment: '',
      show: false,
    };

    //on regarde si la catégorie existe déjà pour ne pas en recréer une identique
    let hasTitreCat = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
         return hasTitreCat = childSnapshot.val().titreCat === 'Ajout de dernière minute';
      });
    });

    if(currentCategory === 'Ajout de dernière minute' && !lastMinuteItem && !hasTitreCat) {
      this.createNewCategory(event);
      this.setState({ lastMinuteItem: true });
    }

    if(newData !== '' && currentTitre !== '') {
      let index = '';
      firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
        snapshot.forEach(function(childSnapshot){
            return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
        });
      });

      firebase.database().ref(`${user}/${currentTitre}/category/${index}/categoryItems`).push({
        ...data
      });
    }
    
    this.animFormButton(todoFormButton);
    todoFormInput.value = '';
  }

  // utilisé sur bouton pour retour en arrière
  goHome = () => {
    this.setState({ clicked: false, isCategory: true });
  }

  // retour au menu de l'accueil
  returnToApp = () => {
    this.setState({ choixComponent: 'menu' });
  }

  // détecte le composant choisi à l'accueil
  choixComponent = (event) => {
    const choixComponent = event.target.name;
    if(choixComponent !== '') {
      if(choixComponent === 'enCourse') {
        this.setState({ choixComponent, currentCategory: 'Ajout de dernière minute' });
      } else {
        this.setState({ choixComponent, currentCategory: 'Ma super catégorie' });
      }
    }
  }

  hideText = () => {
    const clickedText = this.state.clickedText;
    this.setState({ clickedText : !clickedText });
}

deconnexion = event => {
  event.preventDefault();
  firebase.auth().signOut();
  // console.log(firebase.auth().currentUser.displayName);
}

//choix composant via headermenu
handleClick = (e) => {
  const choixComponent = e.key;
  if(choixComponent !== '') {
    if(choixComponent === 'enCourse') {
      this.setState({ choixComponent, currentCategory: 'Ajout de dernière minute' });
    } else {
      this.setState({ choixComponent, currentCategory: 'Ma super catégorie' });
    }
  }
}

  //renvoie dans le render le composent choisit
  renderChoiseConponent = () => {
    const name = this.state.choixComponent;
    switch(name) {
      case 'enCourse':
      return <EnCourse
                returnToApp={this.returnToApp}
                listeDeBase={this.state.listeDeBase}
                choixListe={this.choixListe}
                clicked={this.state.clicked}
                indexItem={this.state.indexItem}
                help={this.state.help}

                isChecked={this.isChecked}
                delete={this.delete}
                toggleComment={this.toggleComment}
                addComment={this.addComment}
                titreListe={this.state.titreListe}
                closeComment={this.closeComment}
                createNewItemLastMinute={this.createNewItemLastMinute}
                goHome={this.goHome}
                currentTitre={this.state.currentTitre}
                data={this.state.data}
                checkCurrentCategory={this.checkCurrentCategory}
                inverseProduit={this.state.inverseProduit}
              />
      case 'mesListes':
      return <Home
                listeDeBase={this.state.listeDeBase}
                choixListe={this.choixListe}
                addNewListTitre={this.addNewListTitre}
                deleteList={this.deleteList}
                loading={this.state.loading}
                data={this.state.data}
                returnToApp={this.returnToApp}
                clicked={this.state.clicked}
                indexItem={this.state.indexItem}
                help={this.state.help}

                isChecked={this.isChecked}
                delete={this.delete}
                toggleComment={this.toggleComment}
                addComment={this.addComment}
                titreListe={this.state.titreListe}
                closeComment={this.closeComment}
                createNewItem={this.createNewItem}
                goHome={this.goHome}
                createNewCategory={this.createNewCategory}
                isCategory={this.state.isCategory}
                currentCategory={this.state.currentCategory}
                toggleSwitch={this.toggleSwitch}
                numChildCategory={this.state.numChildCategory}
                deleteCategory={this.deleteCategory}
                currentTitre={this.state.currentTitre}
                getCurrentCategory={this.getCurrentCategory}
                isToggleSwitchType={this.state.isToggleSwitchType}
                toggleSwitchType={this.toggleSwitchType}
                inverseProduit={this.state.inverseProduit}
              />
      case 'monProfil':
      return <ProfilUser
              returnToApp={this.returnToApp}
              user={this.state.userId}
              toggleSwitchHelp={this.toggleSwitchHelp}
              help={this.state.help}
              toggleSwitchInverseProduit={this.toggleSwitchInverseProduit}
              inverseProduit={this.state.inverseProduit}
              />
      default:
      return null
    }
  }

  render() {
    if (!this.state.isSignedIn) {
      return (
        <Row type="flex" justify="center">
        {/* {console.log('this state isSignedIn n\'existe pas')} */}
          <Col xs={{ span: 16 }} md={{ span: 14}} lg={{ span: 10 }} id='connexion'>
            <h1>Check-List-Course</h1>
            <p>Veuillez vous connecter svp:</p>
          <StyledFirebaseAuth uiConfig={this.uiConfig} firebaseAuth={firebase.auth()} />
          </Col>
        </Row>
      );
    }
    if(this.state.loading) {
      return (
        <Row type="flex" justify="center">
        {this.getDataBase()}
          <Col xs={{ span: 16 }} md={{ span: 14 }} lg={{ span: 10}} id='load'>
            <Loading/>
            <h3>Chargement du caddie</h3>
          </Col>
        </Row>
      )
    }
    if(!this.state.loading) {
        
    }
    
    return (
      <>
      <Row type="flex" justify="space-around" id='main'>
        <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} id='mainCol' >
        <Layout>
          <Header 
            style={{ 
              padding : this.state.largeur >= 480 ? '0 50px' : '0'
            }}>
            <Col xs={0} lg={4}>
              {
                this.state.largeur >= 480 ? 
                <h4><Icon type="shopping-cart" /> Check-Course</h4>
                :
                null
              }
            </Col>
            <Col xs={24} lg={20} 
              style={{
                textAlign : this.state.largeur >= 480 ? 'right' : 'center',
                fontSize : this.state.largeur >= 480 ? '0.9em' : '0.7em'
              }}
            >
                <HeaderMenu
                  handleClick={this.handleClick}
                  choixComponent={this.choixComponent}
                  choixComponentState={this.state.choixComponent}
                />
            </Col>
          </Header>
        <Content>
          
          {
            this.state.choixComponent === 'menu' ?
            <>
              <Col span={24} id={this.state.largeur >= 480 ? 'bckLogo' : 'bckLogo2'}>
                <Icon type="shopping-cart" />
                <h2>CHECK-COURSE</h2>
              </Col>
              <Menu
                choixComponent={this.choixComponent}
                user={this.state.userId}
                hideText={this.hideText}
                clickedText={this.state.clickedText}
                help={this.state.help}
              />
            </>
            :
            this.renderChoiseConponent()
          }
              {/* <Col span={6}>
                  <Button 
                    type='primary'
                    block 
                    onClick={e => this.deconnexion(e)} 
                    className='goBack'
                    >
                      Déconnexion
                  </Button>
              </Col> */}
          </Content>
            <Footer>
              <p>Created By Mikael</p>
            </Footer>
          </Layout>
          </Col>
        </Row>
      </>
    );
  }
}

export default App;
