import React, { Component } from 'react';

import { Button, Icon, Col, notification, Switch } from 'antd';

import ItemList from './ItemList';

const openNotification = () => {
    notification.open({
        message: 'Ajouter une catégorie',
        description: "Pour ajouter une catégorie, cliquez sur \"Ajouter une catégorie\" et écrivez le nom que vous désirez, par exemple \"Boissons\". Pour finaliser, cliquez sur le +.",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        onClose: () => {
            notification.open({
                message: 'Ajouter un produit',
                description: "Pour ajouter un produit, cliquez sur \"Ajouter un produit\" et écrivez le nom que vous désirez, par exemple \"Eau\". Pour finaliser, cliquez sur le +. Vous pouvez ajouter autant de produits que vous le désirez.",
                duration: 0,
                style: {
                    textAlign: 'justify'
                  },
                  onClose: () => {
                    notification.open({
                        message: 'Astuce!',
                        description: "Pour ajouter un produit dans une catégorie existente, cliquez sur le nom de la catégorie voulue, ensuite il vous suffit d'ajouter un produit.",
                        duration: 0,
                        style: {
                            textAlign: 'justify'
                          }
                      });
                  }
              });
        },
      });
  };

class MyLists extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }
    render() { 
        const isCategory = this.props.isCategory;
        return ( 
            
            <>
                <Col xs={{ span: 12, push: 6 }} sm={{ span: 10, push: 6 }} md={{ span: 8, push: 8 }} lg={{ span: 6, push: 9}}>
                    <h3><i className="far fa-list-alt" style={{ color: '#078073', padding: '0.5em'}}></i>{this.props.currentTitre}</h3>
                </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }}>
                        <form className='form form-titre' onSubmit={isCategory === true ? e => this.props.createNewCategory(e) : e => this.props.createNewItem(e)}>
                            <Switch checkedChildren="C" unCheckedChildren="P" checked={isCategory === true ? true : false}  onChange={this.props.toggleSwitch} id='switchButton'/>
                            <input type='text' placeholder={isCategory === true ? 'Ajouter une catégorie' : 'Ajouter un produit'} maxLength={40} />
                            <button type='submit' id='buttonSubmit'>+</button>
                        </form>
                    </Col>
                    <Col span={24}>  
                        <ItemList
                            data={this.props.data}
                            isChecked={this.props.isChecked}
                            delete={this.props.delete}
                            toggleComment={this.props.toggleComment}
                            addComment={this.props.addComment}
                            titre={this.props.titreListe}
                            closeComment={this.props.closeComment}
                            indexItem={this.props.indexItem}
                            currentCategory={this.props.currentCategory}
                            numChildCategory={this.props.numChildCategory}
                            deleteCategory={this.props.deleteCategory}
                            currentTitre={this.props.currentTitre}
                            getCurrentCategory={this.props.getCurrentCategory}
                            inverseProduit={this.props.inverseProduit}
                        />
                    </Col>
                <Col span={6} style={{ marginRight: '1em' }}>
                    <Button block type="primary" onClick={this.props.goHome} className='goBack'>
                        <Icon type="left" />Retour
                    </Button>
                </Col>
            </>
         );
    }
}
 
export default MyLists;