import React, { Component } from 'react';

import { Button, Icon, Row, Col, notification, Switch } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Votre profil',
        description: "Bientôt vous aurez la possibilité de mofifier votre profil. D'ajouter un autre membre de la famille et bien plus encore!",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        // onClose: () => {
        //   alert('clicked');
        // },
      });
  };

class ProfilUser extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }
    render() { 
        const { user, help, inverseProduit } = this.props;
        return ( 
            <Col span={24}>
                <Row type="flex" justify="center">
                    <Col  xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                    <h2>Bienvenue sur votre profile {user}</h2>
                    </Col>
                </Row>
                <Row type="flex" justify="center">
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span>Aide : </span>
                        <Switch checkedChildren="On" unCheckedChildren="Off" checked={help === true ? true : false} onChange={this.props.toggleSwitchHelp} id='switchButtonHelp'/>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                        <span>Inverser l'ordre des produits : </span>
                        <Switch checkedChildren="On" unCheckedChildren="Off" checked={inverseProduit === true ? true : false} onChange={this.props.toggleSwitchInverseProduit} id='switchButtonInverseProduit'/>
                    </Col>
                </Row>
                <Row type='flex' justify='left'>
                    <Col span={6}>
                    <div className='button-anth'>
                        <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                            <Icon type="left" />Retour
                        </Button>
                    </div>
                    </Col>
                </Row>
            </Col>
         );
    }
}
 
export default ProfilUser;